import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import '@/styles/index.less'
import 'vant/lib/index.css'
import 'amfe-flexible'
import '@/utils/dayjs'
import {
  NavBar,
  List,
  Tab,
  Tabs,
  Grid,
  GridItem,
  Form,
  Field,
  Button,
  CountDown,
  Tabbar,
  TabbarItem,
  Icon,
  Cell,
  CellGroup,
  Search,
  Image as VanImage,
  PullRefresh
} from 'vant'
Vue.config.productionTip = false
Vue.use(PullRefresh)
Vue.use(List)
Vue.use(Tab)
Vue.use(Tabs)
Vue.use(Search)
Vue.use(Grid)
Vue.use(GridItem)
Vue.use(Icon)
Vue.use(NavBar)
Vue.use(Tabbar)
Vue.use(TabbarItem)
Vue.use(VanImage)
Vue.use(Button)
Vue.use(Form)
Vue.use(Field)
Vue.use(CountDown)
Vue.use(Cell)
Vue.use(CellGroup)

new Vue({
  router,
  store,
  render: (h) => h(App)
}).$mount('#app')
